.upload-car__color {
	margin-top: 30px;
}

.upload-car__color-title {
	font-size: var(--fz-regular);
	margin-bottom: 30px;
}

.upload-car__color-list {
	display: flex;
	align-items: center;
	flex-wrap: wrap;
}

.upload-car__color-item {
	position: relative;
	margin-right: 21px;

	&:last-child {
		margin-right: 0;
	}

	& input {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		margin: 0;
		opacity: 0;
		z-index: 10;

		&:checked + label:after {
			opacity: 1;
		}
	}

	& label {
		display: block;
		position: relative;
		background: var(--fg-white);
		width: 30px;
		height: 30px;
		border-radius: 50%;
		border: 1px solid #000000;

		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 15px;
			height: 9.32px;
			background: url('../img/icons/icon-checkbox.svg') center center no-repeat;
			background-size: contain;
			opacity: 0;
			transition: all .3s;
		}
	}
}
