.upload-car__menu {
	width: 249px;
	max-width: 100%;
}

.upload-car__menu-item {
	display: block;
	padding: 0;
	border: none;
	background: none;
	color: var(--fg-gray-9);
	margin-bottom: 30px;
	transition: all .3s;
	font-weight: 700;
	font-size: var(--fz-h3);

	&:last-child {
		margin-bottom: 0;
	}

	&._active {
		color: var(--fg-dark);
	}
}
