.profile-upload__car-add {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 23px 40px;
	border: none;
	background: var(--fg-red);
	color: var(--fg-white);
	font-weight: 700;
	font-size: 20px;
	text-transform: uppercase;
	border-radius: 5px;
	width: max-content;
	margin: 0 auto 60px;

	& svg {
		margin-left: 10px;
	}
}
