.profile-image {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 23px;
}

.profile-image__item {
	background-color: var(--fg-gray-9);
	position: relative;
	border-radius: 20px;
	height: 246px;
	cursor: pointer;
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;

	& input {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 10;
		margin: 0;
		cursor: pointer;
	}

	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 104px;
		height: 104px;
		background: url('../img/icons/icon-camera.svg') center center no-repeat;
		background-size: contain;
		transition: all .3s;
	}

	&._selected:after {
		opacity: 0;
	}
}

.profile-image__remove {
	display: inline-block;
	position: absolute !important;
	top: 13px;
	right: 12px;
}
