.register__form {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	gap: 95px;

	& .input {
		margin: 0 0 30px;
	}
}

.register__form-btn {
	height: 72px;
	width: 100%;
}

.register__form-link {
	text-align: center;

	& .auth__form-forgot {
		display: inline-block;
		text-decoration: underline;
	}
}
