.upload-car__actions {
	display: flex;
	align-items: center;
	justify-content: flex-end;
	margin-top: 50px;
}

.upload-car__actions button {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 23px 0;
	background: var(--fg-red);
	color: var(--fg-white);
	border-radius: 5px;
	font-weight: 700;
	font-size: 20px;
	border: 2px solid var(--fg-red);
	text-transform: uppercase;
	width: 299px;
	max-width: 100%;
	margin-right: 35px;
	cursor: pointer;

	&:last-child {
		margin-right: 0;
	}

	& svg {
		margin-left: 10px;
	}
}

button.upload-car__actions--prev {
	background: none;
	color: var(--fg-dark);
	border-color: var(--fg-dark);

	& svg {
		margin-left: 0;
		margin-right: 10px;
	}
}
