.profile__menu {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0;
	margin: 0 0 60px;
	list-style: none;

	& li a {
		display: block;
		color: var(--fg-gray-9);
		font-size: var(--fz-h3);
		font-weight: 700;
		text-transform: uppercase;

		&._active {
			color: var(--fg-dark);
		}
	}
}
