.profile-settings__alert {
	color: var(--fg-red);
	font-size: var(--fz-regular);
	margin-bottom: 30px;
}

.profile-settings__client-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	column-gap: 98px;
	row-gap: 0;
}

.profile-settings__client-actions {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 30px;

	& .input__link {
		margin-top: 0;
	}

	& .btn {
		width: 440px;
		max-width: 100%;
	}
}

.profile-settings__client .profile-form__checkboxes {
	margin-top: 30px;
}
