.profile-settings__avatar {
	width: 250px;
	height: 250px;
	border-radius: 50%;
	background-color: var(--fg-gray-9);
	margin: 0 auto;
	position: relative;
	overflow: hidden;
	cursor: pointer;

	& input {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		z-index: 10;
	}
}

.profile-settings__avatar-icon {
	position: absolute;
	left: 0;
	width: 100%;
	bottom: 0;
	height: 94px;
	background-color: rgba(255, 255, 255, .5);

	&:after {
		content: '';
		position: absolute;
		width: 51px;
		height: 51px;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		background: url('../img/icons/icon-camera.svg') center center no-repeat;
		background-size: contain;
	}
}

.profile__icon-remove {
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background: var(--fg-dark);
	position: relative;
	border: none;
	cursor: pointer;

	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 18px;
		height: 18px;
		background: url('../img/icons/icon-remove.svg') center center no-repeat;
		background-size: contain;
	}
}

.profile-service__add {
	margin-top: 30px;
}
